.App {
  text-align: center;
}

.card-img-top {
  height: 250px;
  object-fit: cover;
  /* object-position: center; */
}

.card {
  height: 33rem;
  margin: 1.5rem 1.5rem !important;
  position: relative;
}

.card-badge {
  display: flex;
  justify-content: flex-end;
  position: absolute;
  right: 0;
  background-color: red;
  color: white;
  border-radius: 10%;
  font-size: 12px;
  font-weight: 500;
  padding: 2px 5px;
}

.card-btn {
  bottom: 3%;
}

.top-headline {
  margin-top: 80px;
}